.singlePost{
    flex: 3;
}
.singlePostWrapper{
    padding: 20px;
    padding-right: 0;
}

.singlePostImage{
    width: 50%;
    height: auto;
    border-radius: 5px;
    /* object-fit: cover; */
}
.singlePostTitle{
    text-align: center;
    font-family: cursive;
    font-size: 28px;
}
.singlePostEdit{
    float: right;
    font-size: 20px;
}
.singlePostIcon{
    margin-left: 10px;
    cursor: pointer;
}
.singlePostIcon:first-child{
    color: teal;
}
.singlePostIcon:last-child{
    color: tomato;
}
.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: #b39656;
    font-style: normal;
}
.singlePostDesc{
    color: #666;
    font-size: 18px;
    line-height: 25px;
}
.singlePostDesc::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}